<template>
    <div>
        <el-progress :percentage="(packets.length / packetsCount) * 100" type="circle" :format="formatPercent"></el-progress>
        <div>{{ packets.length }} / {{ packetsCount }} packets</div>
        <el-tag v-for="(packet, index) in packets" :key="index" size="medium" :type="packet.type">{{packet.name}}</el-tag>
        <el-tag v-for="(name, index) in packetsName" :key="index" size="medium">{{name}}</el-tag>
    </div>
</template>

<script>
    import {names} from "@/data/packetName158";
    import {rawMapping} from "@/data/1_68_0.wfkmap";

    export default {
        name: "PacketsPage",
        mounted() {
            this.packetsCount = names.length;
            let packets = rawMapping.split("\n");
            for (let p of packets) {
                let def = p.split("=");
                if (def.length !== 2) continue;
                let name = def[1].trim();
                let type;
                let index = names.indexOf(name);
                type = 'success'; // tous les packets sont bon s'ils viennent de la map \_()_/
                if (index !== -1) {
                    // type = 'success';
                    names.splice(index, 1)
                } else {
                    // type = '';
                }
                this.packets.push({
                    op: def[0],
                    name: name,
                    type: type
                });
            }
        },
        data() {
            return {
                packetsCount : 0,
                packets: [],
                packetsName: names
            }
        },
        methods: {
            formatPercent(percent) {
                return percent.toFixed(2) + '%';
            }
        }
    }
</script>

